.bodyWrapper {
  height: 450px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.partImage {
  height: 100%;
}

.leftSection {
  vertical-align: top;
  display: inline-block;
  width: 50%;
  padding: 30px;
  padding-top: 140px;
}

.rightSection {
  vertical-align: top;
  display: inline-block;
  width: 50%;
  padding: 30px;
  text-align: right;
  padding-top: 140px;
}

.scoreWrapper {
  line-height: 26px;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 20px;
}

.scoreTitle {
  font-size: 2em;
}

.scoreValue > span:last-child {
  color: red;
  font-size: 20px;
}

.legendStatusWrapper {
  margin-top: 20px;
  color: white;
  width: 150px;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 26px;
}

.legendStatusWrapper > div:last-child {
  color: red;
  font-size: 2em;
  line-height: 30px;
}

.legendRatingWrapper {
  margin-top: 20px;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
}

.legendRatingWrapper > div:first-child {
  line-height: 26px;
}

.legendRatingWrapper > div:last-child {
  background-color: red;
  padding: 0 10px;
  text-align: left;
  width: 100px;
  display: inline-block;
  font-size: 1.8em;
  height: 48px;
}

.button {
  cursor: pointer;
  height: 40px;
  box-shadow: -3px 3px rgba(0, 0, 0, 0.5);
  transition: 0.3s ease;
  margin-right: 20px;
}

.button:hover {
  transform: translate(2px, -2px);
  box-shadow: -5px 5px rgba(0, 0, 0, 0.5);
}

.button:active {
  transform: translate(0px, 0px);
  box-shadow: -3px 3px rgba(0, 0, 0, 0.5);
}

.buttonFooter {
  position: absolute;
  bottom: 30px;
}

.footerRight {
  right: 0px;
}

.desktopFooter {
  display: inline-block;
}
.mobileFooter {
  display: none;
}

@media only screen and (max-width: 520px) {
  .desktopFooter {
    display: none;
  }
  .mobileFooter {
    display: inline-block;
  }
  .leftSection,
  .rightSection {
    font-size: 0.9em !important;
    padding-top: 100px;
  }
}

@media only screen and (max-width: 465px) {
  .leftSection,
  .rightSection {
    font-size: 0.8em !important;
    padding-top: 100px;
  }
  .bodyWrapper {
    height: 350px;
  }
}

@media only screen and (max-width: 370px) {
  .leftSection,
  .rightSection {
    font-size: 0.5em !important;
    padding-top: 100px;
  }
  .legendStatusWrapper {
    width: 100px;
  }
  .legendRatingWrapper > div:last-child {
    height: 30px;
    width: 60px;
  }
  .bodyWrapper {
    height: 250px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}



